.win {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  .img {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .contentText {
    text-align: center;
    color: #FFFFFF;
    min-width: 300px;

    div {
      display: grid;
      grid-template-columns: 1fr 1fr;
      font-size: 25px;
      font-weight: 600;

      @media screen and (max-width: 500px) {
        grid-template-columns: 1fr;
      }

      p:first-child {
        margin-right: 10px;
      }
    }
  }
}