.pin-modal, .exit-modal, .setting-modal, .game-over-modal, .setting-modal-agreement, .gift-modal {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) !important;
  margin: 0 !important;

  .modal-content {
    background: rgba(255, 255, 255, .95);
    border-radius: 14px;

    .pin-title, .exit-title, .setting-title {
      text-align: center;
      font-size: 18px;
      font-weight: 700;
      line-height: 24px;
    }

    .modal-header {
      text-align: center;
    }

    .input-cont {
      .react-code-input {
        width: 190px;
        display: grid !important;
        grid-template: 1fr / repeat(4, 1fr);
        justify-content: space-between;
        margin: 0 auto;

        input{
          padding: 0;
        }
      }

      .description {
        max-width: 250px;
        font-size: 12px;
        line-height: 16px;
        text-align: center;
        color: #979797;
        margin: 18px auto 0;
      }
    }

    .modal-footer {
      height: 52px;
      display: grid;
      grid-template: 1fr / 1fr auto 1fr;
      padding: 0;

      button {
        height: 43px;
        background: transparent;
        border: none;
        font-size: 18px;
        line-height: 24px;
      }

      hr {
        height: 93%;
        width: 1px;
        margin: 0;
        border-left: 1px solid #dee2e6;
      }
    }
  }
}

.pin-modal {
  width: 310px;
}

.exit-modal {
  width: 270px;
}

.setting-modal {
  max-width: 360px;
  padding: 0 20px;

  .stock-switch {
    width: 100%;
    height: 32px;
    display: grid;
    grid-template: 1fr / 1fr 1fr;
    background: rgba(118, 118, 128, 0.12);
    border-radius: 8.91px;

    button {
      width: 100%;
      height: 28px;
      font-weight: 500;
      font-size: 14px;
      line-height: 18px;
      border: none;
      background: transparent;
      align-self: center;
      transition: .08s ease-out;
      outline: none;

      &.active {
        border: 0.5px solid rgba(0, 0, 0, 0.04);
        box-shadow: 0 3px 8px rgba(0, 0, 0, 0.12), 0 3px 1px rgba(0, 0, 0, 0.04);
        border-radius: 6.93px;
        background: #fff;
      }
    }
  }

  .setting-radios {
    display: grid;
    grid-template: 1fr / 1fr 1fr;
    grid-gap: 15px;

    .setting-radio {
      display: flex;
      align-items: center;
      justify-content: space-between;

      p {
        font-size: 16px;
        line-height: 20px;
        margin: 0;
      }

      .field {
        height: 31px;
        width: 51px;
        background: #979797;
        border-radius: 30px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        transition: .2s ease-out;
        padding: 0 2px;

        &.active {
          background: #197532;
          justify-content: flex-end;
        }

        .radio-circle {
          width: 27px;
          height: 27px;
          background: #FFFFFF;
          box-shadow: 0 3px 8px rgba(0, 0, 0, 0.15), 0 3px 1px rgba(0, 0, 0, 0.06);
          border-radius: 50%;
        }
      }
    }
  }

  .modal-footer {
    grid-template: 1fr / 1fr !important;
    justify-content: center;
  }
}

.game-over-modal {
  width: 330px;

  h5 {
    text-align: center;
  }

  .modal-footer {
    height: unset !important;
    display: flex !important;
    justify-content: center;

    .game-over-options {
      display: flex;
      grid-gap: 16px;

      button {
        width: 134px;
        border: 0.5px solid #C4C4C4;
        border-radius: 8px;
        display: flex;
        align-items: center;
        grid-gap: 5px;
        justify-content: center;
      }
    }

    .exit {
      width: 284px;
      height: 56px;
      border-radius: 8px;
      border: 1px solid #197532 !important;
      color: #197532;
    }
  }
}

.setting-modal-agreement {
  min-width: 300px;

  .modal-footer {
    grid-template-columns: 1fr auto 1fr !important;
  }
}

.gift-modal{
  min-width: 320px;
  .gift-close-btn{
    color: #197532;
    border-radius: 10px;
    border: 1px solid #197532 !important;
    padding: 10px 50px;
  }
}
