@import "~bootstrap/dist/css/bootstrap.min.css";

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
  "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  /*-moz-osx-font-smoothing: grayscale;*/
  background: transparent !important;
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
  height: 100%;

  a {
    text-decoration: none;
    outline: none;

    &:hover {
      text-decoration: none;
    }
  }

  button {
    text-decoration: none;
    outline: none;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}


.unselectable {
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

html {
  height: 100%;
  background: #197532;
  background-size: cover;
}

.mainCont {
  display: grid;
  grid-template-rows: 60px 100%;
  grid-gap: 20px;
  height: 100%;
  min-height: 100%;
  max-height: 100%;
  @media only screen and (max-height: 550px) {
    grid-template-rows: 35px 100%;
    grid-gap: 0;
  }
}

.playing-card {
  position: absolute;
  width: 80px;
  height: 125px;
  border-radius: 4px;
  border: 0.5px solid #0D5822;
}

.firstTalonCard {

}

.secondTalonCard {
  left: -30px
}

.thirdTalonCard {
  left: -60px;
}

.head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
  background: rgba(0, 0, 0, 0.2);
}

.desc {
  display: grid;
  grid-template-columns: 80px 1fr;
  justify-content: space-between;
  padding: 0 42px 0;
}

.stocks {
  grid-area: stocks;
  grid-template-columns: 80px;
  position: relative;
  justify-self: flex-end;

  @media(min-width: 940px) {
    grid-template-columns: 1px calc(6vw);
  }
  @media screen and (max-width: 940px) {
    grid-template-columns: 1px calc(80px);
  }
  //@media screen and (max-width: 710px) {
  //  grid-template-columns: 1px calc(70px - 7px);
  //}
  @media only screen and (max-width: 825px) {
    grid-template-columns: 1px calc(50px);
  }

  @media only screen and (max-height:8550px) {
    grid-template-columns: 1px calc(50px);
  }
}

.top-stock {
  display: none;
}

.bottom-stock {
  display: grid;
}

.stock-opened-cards {
  position: absolute;
}

.desc-right {
  display: grid;
  grid-template-areas: "tableu" "stocks";
  grid-template-rows: 1fr 125px;
}

.drop-target {
  position: relative;
  height: 125px;
  width: 80px;
}

.card-border {
  background: rgba(255, 255, 255, 0.1);
  border: .5px solid #0D5822;
  box-sizing: border-box;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-content: center;
  height: 100%;
  width: 100%;

  .cardLoader {
    width: 35%;

    @media screen and (max-width: 550px) {
      width: 55%;
    }
  }
}

.foundations {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  grid-gap: 3px;

  @media(min-width: 940px) {
    border-radius: 7px;
  }
  @media screen and (max-width: 940px) {
    border-radius: 7px;
  }
  @media screen and (max-width: 710px) {
    border-radius: 6px;
  }
  @media only screen and (max-width: 525px) {
    border-radius: 4px;
  }

  @media only screen and (max-height: 550px) {
    border-radius: 4px;
  }
}

.game-timer-container {
  box-sizing: content-box;
  text-align: center;
  align-self: center;
}

.game-timer {
  box-sizing: content-box;
  margin: 0;
  font-size: 1.1rem;
  color: #ffffff;
}

.button-wrapper {
  display: flex;
  grid-gap: 4px;
  justify-content: center;
  align-content: center;
}

.game-button {
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  background: transparent;
  border-radius: 8px;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #FFFFFF;
  border: none;
  padding: 5px 10px;
}

.game-styled-button {
  padding: 8px 22px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  background: rgba(0, 0, 0, 0.2);
}

.svg-icon {
  vertical-align: middle;
  align-self: center;
  max-width: 14px;
}

.game-action-buttons {
  width: 100%;
  padding: 0 42px 0 calc(50% - 50px);
  position: absolute;
  bottom: 30px;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.game-widgets {
  display: flex;
  margin-right: 12px;
  align-items: center;
  align-content: center;
  grid-gap: 18px;
  height: 124.15px;
}

.settings-widget {
  background: transparent;
  border: none;
  outline: none;
}

@media screen and (min-width: 650px) {
  .game-button:hover {
    background-color: rgba(0, 0, 0, 0.09);
  }
}

@media screen and (min-width: 940px) {
  .desc {
    display: grid;
    grid-template-columns: 6vw 1fr;
    justify-content: space-between;
    padding: 0 42px 0;
  }

  .desc-right {
    display: grid;
    grid-template-areas: "tableu" "stocks";
    grid-template-rows: 1fr 9vw;
  }

  .tableu-piles {
    grid-area: tableu;
    justify-self: center;
    display: grid;
    grid-template-columns: repeat(7, 6vw);
    grid-gap: .3vw;
    padding: 0;
  }

  .foundations {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    grid-gap: 3px;
  }

  .drop-target {
    position: relative;
    height: 9vw;
    width: 6vw;
  }

  .playing-card {
    position: absolute;
    width: 6vw;
    height: 9vw;
    border-radius: 7px;
    border: 0.5px solid #0D5822;
  }

  .bottom-stock {
    display: grid;
    width: 9vw;
  }

  .stock-opened-cards {
    position: absolute;
    left: -6.3vw;
  }
}

@media screen and (max-width: 940px) {
  .desc {
    display: grid;
    justify-content: space-between;
    padding: 7px 0 0;
    grid-template-rows: auto 1fr;
    grid-template-columns: 1fr;
    grid-gap: 1vw;
    margin: 0 auto;
  }

  .desc-head {
    display: flex;
    justify-content: space-between;
  }
  .drop-target {
    position: relative;
    height: 124.14px;
    width: 80px;
  }

  .desc-right {
    grid-template-areas: "stocks" "tableu";
    grid-template-rows: auto;
  }

  .tableu-piles {
    margin: 0;
    padding: 0;
    grid-area: tableu;
    justify-self: center;
    display: grid;
    grid-template-columns: repeat(7, 80px);
    grid-gap: .3vw;
  }

  .foundations {
    display: flex;
    flex-direction: row;
    justify-content: start;
    grid-gap: 3px;
    @media only screen and (max-height: 550px) {
      flex-direction: column;
    }
  }

  .top-left-wrapper {
    flex-direction: row;
  }

  .game-widgets {
    display: flex;
    flex-direction: row;
    margin-right: 0;
    align-content: center;
    justify-content: space-around;
    //height: 80px;
  }

  .game-action-buttons {
    width: 578px;
    padding: 0;
    left: 50%;
    transform: translateX(-50%);
  }

  .playing-card {
    width: 80px;
    height: 124.14px;
    position: absolute;
    border-radius: 7px;
    border: 0.5px solid #0D5822;
  }

  .game-timer-container {
    margin-bottom: 0;
  }

  .top-stock {
    display: grid;
    @media only screen and (max-height: 550px) {
      display: none;
    }
  }

  .stock-opened-cards {
    position: absolute;
    left: -84px;
  }

  .bottom-stock {
    display: none;
    @media only screen and (max-height: 550px) {
      display: grid;
      align-items: flex-end;
      padding-bottom: 50px;
    }
  }
}

@media screen and (max-width: 710px) {
  .desc {
    width: 508px;
  }

  .playing-card {
    position: absolute;
    border-radius: 6px;
    border: 0.5px solid #0D5822;
    width: 70px;
    height: 108.63px;
  }

  .drop-target {
    position: relative;
    height: 108.63px;
    width: 70px;
  }

  .tableu-piles {
    display: flex;
    justify-content: space-between;
    justify-self: center;
    grid-template-columns: repeat(7, 6vw);
    grid-gap: .3vw;
    padding: 0;
  }

  .game-action-buttons {
    width: 508px;
  }

  .stock-opened-cards {
    position: absolute;
    left: -73px;
  }
}

@media only screen and (max-width: 525px) {
  .desc {
    width: 98vw;
    display: grid;
    justify-content: space-between;
    padding: 7px 0 0;
    grid-template-rows: auto 1fr;
    grid-template-columns: 1fr;
    grid-gap: 14px;
    margin: 0 auto;
  }

  .svg-icon {
    margin-right: 0;
  }

  .game-timer-container {
    margin-bottom: 0;
  }

  .game-widgets {
    margin-right: 0;
  }

  .playing-card {
    width: 50px;
    height: 80px;
    border-radius: 4px;
  }

  .game-action-buttons {
    width: 298px;
  }

  .drop-target {
    position: relative;
    height: 80px;
    width: 50px;
  }

  .card-border {
    height: 99%;
    width: 99%;
  }

  .stock-opened-cards {
    position: absolute;
    left: -52px;
  }
}

@media only screen and (max-height: 550px) {
  .desc {
    max-height: calc(100vh - 60px);
    width: unset;
    display: grid;
    justify-content: space-between;
    padding: 7px 0 0;
    grid-template-rows: auto;
    grid-gap: 25px;
    margin: 0 auto;
    grid-template-columns: 50px 1fr;
  }

  .svg-icon {
    margin-right: 0;
  }

  .game-timer-container {
    margin-bottom: 0;
  }

  .game-widgets {
    margin-right: 0;
  }

  .tableu-piles {
    grid-template-columns: repeat(7, 50px);
    grid-gap: .3vw;
  }

  .playing-card {
    width: 50px;
    height: 80px;
    border-radius: 4px;
  }

  .game-action-buttons {
    width: 298px;
  }

  .drop-target {
    position: relative;
    height: 80px;
    width: 50px;
  }

  .card-border {
    height: 99%;
    width: 99%;
  }

  .stock-opened-cards {
    position: absolute;
    left: -55px;
    bottom: 50px;
  }
}

.card-backside {
  box-sizing: border-box;
  border: 1px solid #0D5822;
  background: #fff;
  border-radius: 7px;
  padding: 4px;
  @media screen and (max-width: 710px) {
    padding: 3px;
    border-radius: 6px;
  }
  @media only screen and (max-width: 525px) {
    padding: 3px;
    border-radius: 4px;
  }
  @media only screen and (max-height: 550px) {
    padding: 3px;
    border-radius: 4px;
  }

  img {
    border-radius: inherit;
    @media (min-width: 940px) {
      height: calc(9vw - 10px);
      width: calc(6vw - 10px);
    }
    @media screen and (max-width: 940px) {
      height: calc(124.14px - 10px);
      width: calc(80px - 10px);
    }
    @media screen and (max-width: 710px) {
      height: calc(108.63px - 7px);
      width: calc(70px - 7px);
    }
    @media only screen and (max-width: 525px) {
      height: calc(80px - 8px);
      width: calc(50px - 8px);
    }
    @media only screen and (max-height: 550px) {
      height: calc(80px - 8px);
      width: calc(50px - 8px);
    }
  }
}

.score {
  color: #fff;
  font-size: 1.1em;
}

@media only screen and (max-height: 550px) {
  .desc-right {
    display: grid;
    grid-template-areas:
        "tableu stocks";
    grid-template-columns: 1fr 50px;
    grid-template-rows: auto;
    justify-self: center;
    grid-gap: 110px;
  }

  .game-action-buttons {
    position: fixed;
    bottom: 10px;
  }
}

@media only screen and (max-width: 500px) {
  .desc-right {
    display: block;
    width: 100%;
  }
}
